import { File, Folder, davParsePermissions } from '@nextcloud/files';
import { generateRemoteUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import { getClient, rootPath } from './WebdavClient';
import { getDavNameSpaces, getDavProperties, getDefaultPropfind } from './DavProperties';
const client = getClient();
const reportPayload = `<?xml version="1.0"?>
<oc:filter-files ${getDavNameSpaces()}>
	<d:prop>
		${getDavProperties()}
	</d:prop>
	<oc:filter-rules>
		<oc:favorite>1</oc:favorite>
	</oc:filter-rules>
</oc:filter-files>`;
const resultToNode = function (node) {
    const props = node.props;
    const permissions = davParsePermissions(props?.permissions);
    const owner = getCurrentUser()?.uid;
    const nodeData = {
        id: props?.fileid || 0,
        source: generateRemoteUrl('dav' + rootPath + node.filename),
        mtime: new Date(node.lastmod),
        mime: node.mime,
        size: props?.size || 0,
        permissions,
        owner,
        root: rootPath,
        attributes: {
            ...node,
            ...props,
            hasPreview: props?.['has-preview'],
        },
    };
    delete nodeData.attributes.props;
    return node.type === 'file'
        ? new File(nodeData)
        : new Folder(nodeData);
};
export const getContents = async (path = '/') => {
    const propfindPayload = getDefaultPropfind();
    // Get root folder
    let rootResponse;
    if (path === '/') {
        rootResponse = await client.stat(path, {
            details: true,
            data: getDefaultPropfind(),
        });
    }
    const contentsResponse = await client.getDirectoryContents(path, {
        details: true,
        // Only filter favorites if we're at the root
        data: path === '/' ? reportPayload : propfindPayload,
        headers: {
            // Patched in WebdavClient.ts
            method: path === '/' ? 'REPORT' : 'PROPFIND',
        },
        includeSelf: true,
    });
    const root = rootResponse?.data || contentsResponse.data[0];
    const contents = contentsResponse.data.filter(node => node.filename !== path);
    return {
        folder: resultToNode(root),
        contents: contents.map(resultToNode),
    };
};
